<template>
<div id="app" v-turn>
    <audio class="audio">
        <source src="/static/fy.mp3" type="audio/ogg"></source>
    </audio>
    <div class="flipbook-viewport">
        <div class="container1">
            <div class="flipbook">
                <div :style="'position: absolute;background-image:url(http://fitment.gz-studio.com.cn' + item.url + ')'" v-for="(item, index) in items" :key="index">
                    <div v-if="item.hotlink.length">
                        <div class="hover-scale hotlink" v-for="(hotlink, h) in item.hotlink" :key="h" :style="hotlink.attr[0].style">
                            <a href="" class="hotlink-link" :style="'width:'+ hotlink.attr[0].width+ 'px;height:'+ hotlink.attr[0].height+'px'"></a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import '../directives/turn'
import axios from 'axios'
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';
import qs from 'qs'

export default {
    data() {
        return {
            items: []
        }
    },
    methods: {
        getParams(key) {
            var url = location.search.replace(/^\?/,'').split('&');
            var paramsObj = {};
            for(var i = 0, iLen = url.length; i < iLen; i++){
                var param = url[i].split('=');
                paramsObj[param[0]] = param[1];
            }
            if(key){
                return paramsObj[key] || '';
            }
            return paramsObj;
        },
        getUrlParam(name){
            var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
            var r = window.location.search.substr(1).match(reg);
            if (r!=null) return unescape(r[2]); return null;
        },
        async getList() {
            let { data } = await axios.post('/picApi/factoryAlbumInfo', qs.stringify({
                id: this.getUrlParam('id')
            }))
            this.item = data.result.album
            this.items = data.result.album.pic
        },
        async share() {
            let { data } = await axios.post('/wxApi/shareConfig')
        }
    },
    created() {
        this.getList()
    },
    mounted() {
        //this.share()
        // wx.config({
        //     debug: true,
        //     appId: '', // 必填，公众号的唯一标识
        //     timestamp: , // 必填，生成签名的时间戳
        //     nonceStr: '', // 必填，生成签名的随机串
        //     signature: '',// 必填，签名，见附录1
        //     jsApiList: [] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        // });
    }
}
</script>

<style>
body, html { height: 100%; width: 100%;}
#app, .app {
    height: 100%;
    width: 100%;
}
</style>



